@import url(https://fonts.googleapis.com/css2?family=Exo:wght@300;400;500;600&display=swap);
.progress-bar{height:100%;width:100%;background-color:var(--color-gray-02);transition:all 0.15s ease;border-radius:4px;position:relative}.progress-bar-line{width:100%;height:4px;border-radius:4px;background-color:var(--color-primary)}.progress-bar-container{margin-top:4rem;width:100%;height:4px;background-color:transparent}.progress-bar-txt{margin:0 0 0.5rem;text-align:center}

.ocr-file-upload{margin:1rem 0;display:flex;align-items:center;justify-content:center;flex-direction:column;width:100%;border:2px solid var(--color-gray-02);background-color:var(--color-gray-01);padding:4rem 1rem;border-radius:8px;position:relative}.ocr-file-upload-container{display:flex;align-items:center;grid-gap:1rem;gap:1rem}.ocr-file-upload-container-txt{width:11rem}.ocr-file-upload-container-txt-title{margin:0.25rem 0}.ocr-file-upload-container-txt-desc{color:var(--color-gray-03);font-size:0.825rem;margin:0}.ocr-file-upload .icon-file-upload,.ocr-file-upload .icon-file-upload-success{width:4rem;height:4rem;fill:var(--color-white)}.ocr-file-upload .icon-file-upload{margin:0.75rem 0 0}.ocr-file-upload-input{cursor:pointer;opacity:0;position:absolute;height:100%;width:100%}

.image-wrapper{height:100%;border-radius:5px;background-color:var(--color-gray-01);border:2px solid var(--color-gray-02);width:100%;height:400px}.image-wrapper-text{height:100%;display:flex;align-items:center;justify-content:center;grid-gap:1rem;gap:1rem;padding:0 2rem}.image-wrapper-text h3{margin:0}.image-wrapper-text-desc{color:var(--color-gray-03);font-size:0.825rem}.image-wrapper-text .icon-image-preview{width:4.5rem;height:4.5rem;fill:var(--color-white)}.image-wrapper img{border-radius:5px;width:100%;height:100%;position:relative}

.ocr-by-link{width:100%;display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;margin:1rem 0}.ocr-by-link-input{color:var(--color-white);border:2px solid var(--color-gray-02);background-color:var(--color-gray-01);border-radius:8px;padding:0.75rem;outline:none}.ocr-by-link-input:-ms-input-placeholder{color:var(--color-gray-03)}.ocr-by-link-input::placeholder{color:var(--color-gray-03)}

.icon-button{outline:none;padding:0.625rem;border-radius:8px;transition:0.25s;display:flex;align-items:center;border:1px solid var(--color-gray-02);background-color:var(--color-gray-01);cursor:pointer}.icon-button:hover{border-color:var(--color-primary)}.icon-button:hover svg{fill:var(--color-primary)}.icon-button svg{fill:var(--color-gray-03);width:0.825rem;height:0.825rem}.icon-button-container{position:relative}.icon-button-container:hover .icon-button-tooltip{opacity:1;visibility:visible}.icon-button-tooltip{z-index:1;opacity:0;visibility:hidden;position:absolute;top:0.25rem;left:3rem;border-radius:4px;padding:0.25rem 0.5rem;font-size:0.825rem;color:var(--color-gray-03);border:1px solid var(--color-gray-02);background-color:var(--color-gray-01);white-space:nowrap}.icon-button-tooltip-left{left:unset;right:calc(100% + 0.75rem)}.icon-button-tooltip-left::before{top:0.5rem;right:-0.5rem;position:absolute;content:"";width:0.5rem;height:0.5rem;background-color:var(--color-gray-02);-webkit-clip-path:polygon(0 0, 0% 100%, 100% 50%);clip-path:polygon(0 0, 0% 100%, 100% 50%)}.icon-button-tooltip-right::before{top:0.5rem;left:-0.5rem;position:absolute;content:"";width:0.5rem;height:0.5rem;background-color:var(--color-gray-02);-webkit-clip-path:polygon(0 50%, 100% 100%, 100% 0);clip-path:polygon(0 50%, 100% 100%, 100% 0)}

.text-wrapper{display:flex;flex-direction:column;align-items:flex-end;width:100%;margin:0 0 1rem}.text-wrapper-icons{display:flex;grid-gap:0.75rem;gap:0.75rem;margin-bottom:0.75rem}textarea{width:100%;border:2px solid var(--color-gray-02);color:var(--color-white);background-color:var(--color-gray-01);border-radius:5px;padding:1rem;box-sizing:border-box;resize:none;font-family:var(--font)}textarea:focus{outline:none}

.btn-outline{padding:0.5rem;width:100%;outline:none;color:var(--color-background);border:none;font-family:var(--font);background:var(--color-primary);font-size:1.125rem;font-weight:600;border-radius:8px;cursor:pointer;box-shadow:0 4px 4px #0a0a0a}.btn-outline:disabled{cursor:not-allowed;color:var(--color-primary);border:1px solid var(--color-primary);background-color:var(--color-background)}

.languages{display:flex;flex-wrap:wrap}.languages span{color:var(--color-gray-03)}.option-active{font-weight:600;color:var(--color-background);background-color:var(--color-primary)}.languages-select{position:relative;cursor:pointer}.languages-select .selected-language{width:12.5rem;padding:0.25rem 0.5rem;border-radius:4px;border:2px solid var(--color-gray-02);background-color:var(--color-gray-01);display:flex;align-items:center;justify-content:space-between}.languages-select .selected-language .icon-chevron-down{fill:var(--color-white);width:1rem;height:1rem}.languages-select ul{top:2.25rem;position:absolute;list-style:none;background-color:var(--color-gray-01);border:2px solid var(--color-gray-02);border-radius:4px;overflow-y:auto;margin:0;padding:0;width:12.5rem;height:16rem;z-index:1}.languages-select li{padding:0.25rem 0.5rem}


.main-content{display:flex;flex-direction:column;align-items:center;justify-content:center;max-width:32rem;padding:0 1rem;margin:1rem auto}.convert-options{display:flex;align-items:center;justify-content:center;margin:0 0 1rem;position:relative;width:100%}.convert-options::before{position:absolute;content:"";bottom:0;width:100%;height:2px;z-index:-1;background-color:var(--color-gray-02)}.convert-option{border:none;outline:none;background-color:transparent;font-size:1.25rem;font-family:var(--font);font-weight:500;color:var(--clor-white);margin:0;padding:0.5rem 0;border-bottom:2px solid transparent;width:8rem;text-align:center;cursor:pointer}.convert-option:disabled{cursor:not-allowed}.active.convert-option{border-bottom:2px solid var(--color-primary)}.languages{display:flex;align-items:center;justify-content:space-between;grid-gap:1rem;gap:1rem}.languages select{border:2px solid var(--color-gray-02);background-color:var(--color-gray-01);padding:0.5rem;border-radius:4px;outline:none;color:var(--color-white)}.error{margin:0 0 1rem;display:flex;align-items:center;justify-content:space-between;font-size:0.925rem;width:100%;border:1px solid var(--color-primary-two);background-color:var(--color-primary-three);border-radius:4px;padding:0.5rem}.error-message{color:var(--color-primary-two);display:flex;align-items:center;grid-gap:0.5rem;gap:0.5rem}.error .icon-close{cursor:pointer}


:root {
  --hue: 160;
  --color-background: hsl(0, 0%, 7%);
  --color-gray-01: hsl(0, 0%, 8%);
  --color-gray-02: hsl(0, 0%, 12%);
  --color-gray-03: hsl(0, 0%, 60%);

  --color-white: hsl(0, 0%, 100%);
  --color-text: hsl(0, 0%, 100%);
  --color-primary: hsl(var(--hue), 60%, 65%);
  --color-primary-two: hsl(var(--hue), 60%, 75%);
  --color-primary-three: hsla(var(--hue), 60%, 75%, 0.175);
  --font: "Exo", sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: hsl(0, 0%, 7%);
  background-color: var(--color-background);
  color: hsl(0, 0%, 100%);
  color: var(--color-text);
  font-family: "Exo", sans-serif;
  font-family: var(--font);
  background-image: radial-gradient(
    hsla(0, 0%, 48%, 0.075) 1px,
    transparent 1px
  );
  background-size: 0.825rem 0.825rem;
}

* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 4px;
}

::-webkit-scrollbar-track {
  background: hsl(0, 0%, 8%);
  background: var(--color-gray-01);
}

::-webkit-scrollbar-thumb {
  background: hsl(0, 0%, 12%);
  background: var(--color-gray-02);
}

